/*! tailwindcss v4.1.17 | MIT License | https://tailwindcss.com */
.visible {
  visibility: visible;
}
.container {
  width: 100%;
}
.block {
  display: block;
}
.hidden {
  display: none;
}
.text-center {
  text-align: center;
}
.lowercase {
  text-transform: lowercase;
}
.uppercase {
  text-transform: uppercase;
}
:root {
  --navy: #22314b;
  --navy-2: #2c3f5e;
  --teal: #2fc4b6;
  --teal-2: #27b0a5;
  --cream: #fff6ef;
  --cream-2: #f3e8de;
  --ink: var(--navy);
  --ink-2: #4a5a73;
  --muted: #6f7b91;
  --note: #ffffff;
  --border: #e6dcd2;
  --glass: rgba(47, 196, 182, 0.1);
  --primary: var(--teal);
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
html,
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;
  background-color: var(--cream);
  color: var(--ink);
}
a {
  color: inherit;
  text-decoration: none;
}
button {
  font-family: inherit;
}
.ss-main {
  min-height: 100vh;
  background-color: var(--cream);
  padding-top: 64px;
}
.ss-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 64px;
  padding: 0 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(18px);
  background: rgba(255, 246, 239, 0.9);
  border-bottom: 1px solid var(--border);
  z-index: 20;
}
.ss-header-inner {
  width: 100%;
  max-width: 1200px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.ss-brand {
  display: flex;
  align-items: center;
  gap: 0.65rem;
}
.ss-brand,
.ss-brand:focus,
.ss-brand:hover,
.ss-brand:active {
  text-decoration: none;
  border: none;
  box-shadow: none;
  outline: none;
}
.ss-brand-logo {
  width: 60px;
  height: 60px;
  border-radius: 999px;
  overflow: hidden;
  border: 2px solid var(--teal);
  background: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ss-brand-logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.ss-brand-text {
  font-weight: 700;
  font-size: 1.15rem;
  color: var(--navy);
}
.ss-nav {
  display: flex;
  align-items: center;
  gap: 2.25rem;
}
.ss-nav-link {
  position: relative;
  padding-bottom: 2px;
  font-size: 0.95rem;
  font-weight: 600;
  color: var(--navy);
}
.ss-nav-link:hover {
  color: var(--navy-2);
}
.ss-nav-link::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: -4px;
  height: 2px;
  border-radius: 999px;
  background: linear-gradient(to right, var(--teal), var(--navy));
  transform: scaleX(0);
  transform-origin: center;
  transition: transform 0.15s ease-out;
}
.ss-nav-link:hover::after {
  transform: scaleX(1);
}
.ss-header-actions {
  display: flex;
  align-items: center;
  gap: 0.6rem;
}
.ss-btn,
.ss-btn-outline {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.65rem 1.7rem;
  border-radius: 999px;
  font-size: 0.95rem;
  font-weight: 600;
  cursor: pointer;
  text-decoration: none;
  transition: transform 0.12s ease, box-shadow 0.12s ease, background 0.12s ease,
    color 0.12s ease, border-color 0.12s ease;
}
.ss-btn {
  background: var(--primary);
  color: #ffffff;
  border: 1px solid var(--primary);
  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.12);
}
.ss-btn:hover {
  background: var(--teal-2);
  border-color: var(--teal-2);
  transform: translateY(-1px);
  box-shadow: 0 14px 30px rgba(0, 0, 0, 0.18);
}
.ss-btn-outline {
  background: transparent;
  color: var(--navy);
  border: 1px solid var(--navy-2);
  text-decoration: none;
}
.ss-btn-outline:hover {
  background: rgba(255, 255, 255, 0.7);
  text-decoration: none;
}
.ss-nav-btn {
  padding: 0.45rem 1.15rem;
  font-size: 0.86rem;
  box-shadow: none;
}
.hero.hero-split {
  display: grid;
  grid-template-columns: 1fr 1fr;
  min-height: calc(100vh - 64px);
  background: var(--cream);
}
.hero-media-col {
  position: relative;
  overflow: hidden;
}
.hero-media {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.hero-content-col {
  position: relative;
  display: flex;
  align-items: center;
}
.hero-inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 3.5rem 2.5rem 3.5rem;
  display: flex;
  align-items: center;
}
.hero-card {
  background: transparent;
  border-radius: 18px;
  border: none;
  box-shadow: none;
  padding: 2.2rem 2.8rem 2.4rem;
  max-width: 640px;
  position: relative;
}
.hero-card::before {
  content: "";
  position: absolute;
  left: 0;
  top: 1.1rem;
  bottom: 1.1rem;
  width: 3px;
  border-radius: 999px;
  background: linear-gradient(to bottom, var(--teal), var(--navy));
}
.hero-title {
  margin: 0 0 0.9rem 0.9rem;
  font-size: 2.5rem;
  line-height: 1.15;
  font-weight: 800;
  color: #111827;
}
.hero-copy {
  margin: 0.25rem 0 0;
  margin-left: 0.9rem;
  font-size: 1rem;
  line-height: 1.6;
  color: #111827;
  max-width: 38rem;
}
.hero-title-landing {
  color: var(--teal);
  text-shadow: 0 2px 14px rgba(0, 0, 0, 0.12);
}
.hero-tagline-landing {
  margin-left: 0.9rem;
  margin-top: -0.55rem;
  font-weight: 900;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #111827;
}
@media (max-width: 960px) {
  .ss-header {
    padding: 0 1.25rem;
  }
  .hero-inner {
    padding: 3rem 1.5rem 3rem;
  }
  .hero-card {
    padding: 1.7rem 1.8rem 2rem;
  }
  .hero-title {
    font-size: 2.05rem;
  }
}
@media (max-width: 768px) {
  .hero.hero-split {
    grid-template-columns: 1fr;
    min-height: auto;
  }
  .hero-media-col {
    height: 46vh;
  }
}
.text-muted-strong {
  color: var(--ink-2);
}
.shadow-soft {
  box-shadow: 0 10px 30px rgba(34, 49, 75, 0.12);
}
.section-deep {
  background: linear-gradient(180deg, var(--cream), #fff);
}
.features-title {
  color: var(--ink);
  font-size: 1.9rem;
  font-weight: 900;
  text-align: center;
  margin: 60px 0 30px;
  text-transform: uppercase;
  letter-spacing: 1px;
  animation: fadeInUp 1s ease-in-out;
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.feature-card {
  background: #fff;
  border: 1px solid var(--cream-2);
  border-radius: 18px;
  box-shadow: 0 14px 32px rgba(34, 49, 75, 0.14);
  position: relative;
  overflow: hidden;
  opacity: 0;
  animation: fadeInUp 0.7s ease forwards;
}
.feature-card::before {
  content: "";
  position: absolute;
  inset: 0 0 auto 0;
  height: 6px;
  background: linear-gradient(90deg, var(--teal), var(--teal-2));
}
.icon-badge-xl {
  width: 78px;
  height: 78px;
  display: grid;
  place-items: center;
  margin-inline: auto;
  border-radius: 50%;
  background: var(--teal);
  color: #fff;
  box-shadow: 0 10px 26px rgba(47, 196, 182, 0.35), inset 0 -6px 12px rgba(0, 0, 0, 0.12);
  border: 3px solid #fff;
  outline: 2px solid var(--teal-2);
  transition: transform 0.18s ease, box-shadow 0.18s ease;
}
.icon-badge-xl i {
  font-size: 30px;
  line-height: 1;
}
.feature-card:hover .icon-badge-xl {
  transform: translateY(-3px) scale(1.03);
  box-shadow: 0 16px 36px rgba(47, 196, 182, 0.45), inset 0 -8px 14px rgba(0, 0, 0, 0.14);
}
#features .feature-heading {
  color: var(--navy) !important;
  font-weight: 900 !important;
  letter-spacing: 0.2px;
  margin-bottom: 0.35rem;
}
#features p {
  color: var(--ink-2);
}
#features .col-md-4:nth-child(1) .feature-card {
  animation-delay: 0.12s;
}
#features .col-md-4:nth-child(2) .feature-card {
  animation-delay: 0.28s;
}
#features .col-md-4:nth-child(3) .feature-card {
  animation-delay: 0.44s;
}
.partner-logo {
  height: 65px;
  width: auto;
  max-width: 100%;
  object-fit: contain;
  filter: drop-shadow(0 3px 8px rgba(0, 0, 0, 0.12));
}
.text-accent {
  color: var(--teal) !important;
}
.feature-bullet {
  display: flex;
  align-items: flex-start;
  gap: 0.5rem;
  margin-bottom: 0.6rem;
  color: var(--ink-2);
  font-size: 0.97rem;
}
.feature-bullet i {
  color: var(--teal);
  font-size: 1.15rem;
  background: var(--glass);
  padding: 6px;
  border-radius: 8px;
  line-height: 1;
  box-shadow: 0 4px 10px rgba(47, 196, 182, 0.2);
  transition: all 0.2s ease;
}
.feature-bullet:hover i {
  transform: translateY(-2px);
  box-shadow: 0 6px 14px rgba(47, 196, 182, 0.35);
}
.footer-ss .footer-logo {
  filter: drop-shadow(0 2px 6px rgba(0, 0, 0, 0.08));
}
.footer-ss .footer-icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 38px;
  height: 38px;
  border-radius: 50%;
  background: #fff;
  border: 1px solid var(--cream-2);
  color: var(--teal);
  margin: 0 3px;
  transition: all 0.3s ease;
}
.footer-ss .footer-icon:hover {
  background: var(--cream);
  color: var(--teal-2);
  transform: translateY(-2px);
}
.ss-nav-desktop {
  display: flex;
}
.ss-menu-toggle {
  display: none;
}
.ss-mobile-menu {
  display: none;
}
@media (max-width: 768px) {
  .ss-nav-desktop {
    display: none;
  }
  .ss-menu-toggle {
    display: flex;
    background: transparent;
    border: none;
    padding: 0;
    width: 32px;
    height: 32px;
    cursor: pointer;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 4px;
  }
  .ss-menu-toggle span {
    display: block;
    width: 20px;
    height: 2px;
    border-radius: 999px;
    background: var(--navy);
    transition: transform 0.15s ease, opacity 0.15s ease;
  }
  .ss-menu-toggle.is-open span:nth-child(1) {
    transform: translateY(6px) rotate(45deg);
  }
  .ss-menu-toggle.is-open span:nth-child(2) {
    opacity: 0;
  }
  .ss-menu-toggle.is-open span:nth-child(3) {
    transform: translateY(-6px) rotate(-45deg);
  }
  .ss-mobile-menu {
    position: fixed;
    top: 64px;
    left: 0;
    right: 0;
    padding: 1rem 1.25rem 1.5rem;
    background: rgba(255, 246, 239, 0.98);
    border-bottom: 1px solid var(--border);
    display: none;
    flex-direction: column;
    gap: 0.75rem;
    z-index: 19;
    box-sizing: border-box;
    overflow-x: hidden;
  }
  .ss-mobile-menu.is-open {
    display: flex;
  }
  .ss-mobile-link {
    font-size: 0.95rem;
    font-weight: 600;
    color: var(--navy);
  }
  .ss-mobile-actions {
    margin-top: 0.5rem;
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
  }
}
.ss-nav-link {
  text-decoration: none !important;
}
.ss-nav-link:hover {
  text-decoration: none !important;
}
.hero-tagline {
  margin: 0 0 0.9rem 0.9rem;
  font-weight: 900;
  letter-spacing: .08em;
  text-transform: uppercase;
  color: #111827;
}
.footer-ss {
  background: var(--cream) !important;
  color: var(--ink) !important;
}
.footer-ss .btn-link {
  color: var(--teal) !important;
}
.footer-ss .btn-link:hover {
  color: var(--teal-2) !important;
  text-decoration: underline;
}
.footer-ss .small {
  color: var(--ink-2) !important;
}
.hero-split {
  position: relative;
  height: clamp(520px, 70vh, 740px);
  overflow: hidden;
  display: flex;
  align-items: center;
  background: var(--cream);
}
.hero-split-media {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0;
}
.hero-split-tint {
  position: absolute;
  inset: 0;
  background: linear-gradient(
    to right,
    rgba(255, 246, 239, 0.55) 0%,
    rgba(255, 246, 239, 0.35) 45%,
    rgba(255, 246, 239, 0.15) 70%,
    rgba(255, 246, 239, 0.02) 100%
  );
  z-index: 1;
}
.hero-split-inner {
  position: relative;
  z-index: 2;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2.5rem;
}
.hero-split-card {
  position: relative;
  max-width: 760px;
  padding: 1.6rem 2.2rem 1.8rem;
  background: transparent;
  border: none;
  box-shadow: none;
}
.hero-split-card::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0.9rem;
  bottom: 0.9rem;
  width: 3px;
  border-radius: 999px;
  background: linear-gradient(to bottom, var(--teal), var(--navy));
}
.hero-split-title {
  margin: 0 0 0.9rem 0.9rem;
  font-size: clamp(2.1rem, 4vw, 3rem);
  line-height: 1.12;
  font-weight: 800;
  color: var(--navy);
}
.hero-split-title span {
  color: var(--teal);
}
.hero-split-copy {
  margin: 0.25rem 0 0;
  margin-left: 0.9rem;
  font-size: 1rem;
  line-height: 1.65;
  color: var(--navy);
  max-width: 46rem;
}
.hero-split-actions {
  margin-top: 1.4rem;
  margin-left: 0.9rem;
  display: flex;
  flex-wrap: wrap;
  gap: 0.9rem;
}
.hero-split-cta,
.hero-split-cta-outline {
  padding: 0.9rem 2.1rem;
  font-size: 1rem;
}
@media (max-width: 960px) {
  .hero-split-inner {
    padding: 0 1.5rem;
  }
  .hero-split-card {
    padding: 1.3rem 1.6rem 1.4rem;
  }
}
@media (max-width: 640px) {
  .hero-split {
    height: clamp(520px, 78vh, 820px);
  }
  .hero-split-actions {
    flex-direction: column;
    align-items: flex-start;
  }
  .hero-split-card::before {
    left: -2px;
  }
}
.q-head {
  margin-bottom: 1.1rem;
}
.q-head-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: .75rem;
}
.q-ghost {
  border: 1px solid var(--border);
  background: rgba(255,255,255,.75);
  color: var(--navy);
  font-weight: 750;
  border-radius: 12px;
  padding: .5rem .75rem;
  transition: transform .14s ease, box-shadow .14s ease, border-color .14s ease;
}
.q-ghost:hover {
  transform: translateY(-1px);
  border-color: rgba(47,196,182,.35);
  box-shadow: 0 14px 30px rgba(34,49,75,.10);
}
.q-progress {
  margin-top: .75rem;
  height: 10px;
  border-radius: 999px;
  background: rgba(34,49,75,.08);
  overflow: hidden;
  border: 1px solid var(--border);
}
.q-progress-bar {
  height: 100%;
  border-radius: 999px;
  background: linear-gradient(to right, var(--teal), var(--navy));
  transition: width .28s ease;
}
.q-step {
  display: grid;
  gap: .9rem;
  margin-top: 1rem;
}
.q-card {
  border: 1px solid var(--border);
  border-radius: 18px;
  padding: 1rem;
  background: #fff;
  animation: ssfade .18s ease;
}
@keyframes ssfade {
  from {
    opacity: .0;
    transform: translateY(6px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.q-card-title {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: .75rem;
  margin-bottom: .75rem;
}
.q-title {
  font-weight: 900;
  color: var(--navy);
  letter-spacing: -.01em;
}
.q-sub {
  margin-top: .25rem;
}
.q-required {
  font-size: .75rem;
  font-weight: 850;
  color: var(--navy);
  background: rgba(47,196,182,.10);
  border: 1px solid rgba(47,196,182,.25);
  border-radius: 999px;
  padding: .25rem .6rem;
  white-space: nowrap;
}
.q-options {
  display: grid;
  grid-template-columns: repeat(2,minmax(0,1fr));
  gap: .6rem;
}
.q-opt {
  display: flex;
  align-items: center;
  gap: .6rem;
  border: 1px solid var(--border);
  border-radius: 16px;
  padding: .75rem .8rem;
  background: rgba(255,246,239,.45);
  font-weight: 800;
  color: var(--navy);
  transition: transform .14s ease, box-shadow .14s ease, border-color .14s ease, background .14s ease;
}
.q-opt i {
  font-size: 1.05rem;
  color: var(--teal);
}
.q-opt:hover {
  transform: translateY(-1px);
  border-color: rgba(47,196,182,.35);
  box-shadow: 0 14px 30px rgba(34,49,75,.10);
}
.q-opt.is-active {
  background: rgba(47,196,182,.10);
  border-color: rgba(47,196,182,.45);
}
.q-check {
  margin-left: auto;
  color: var(--teal);
}
.q-error {
  margin-top: .9rem;
  padding: .85rem .95rem;
  border-radius: 16px;
  background: rgba(220,38,38,.06);
  border: 1px solid rgba(220,38,38,.18);
  color: #991b1b;
  font-weight: 750;
}
.q-actions {
  margin-top: 1.1rem;
  display: flex;
  justify-content: space-between;
  gap: .75rem;
  flex-wrap: wrap;
}
.q-range input[type="range"] {
  width: 100%;
}
.q-range-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: .35rem;
}
.q-range-value {
  font-weight: 900;
  color: var(--navy);
  background: rgba(47,196,182,.10);
  border: 1px solid rgba(47,196,182,.25);
  border-radius: 999px;
  padding: .15rem .6rem;
}
.match-shell {
  min-height: 100vh;
  padding: 6rem 1.25rem 2.25rem;
  background: var(--cream);
  display: flex;
  justify-content: center;
}
.match-inner {
  width: 100%;
  max-width: 920px;
}
.match-top {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  gap: 1rem;
  flex-wrap: wrap;
}
.match-title {
  margin: 0;
  color: #111827;
  font-weight: 950;
  letter-spacing: -.02em;
}
.match-edit {
  border: 1px solid var(--border);
  background: #fff;
  border-radius: 14px;
  padding: .6rem .9rem;
