  gap: .75rem;
  margin-top: 1rem;
}
.match-reason-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 800;
  color: var(--navy);
}
.match-bar {
  height: 10px;
  border-radius: 999px;
  background: rgba(34,49,75,.08);
  overflow: hidden;
  border: 1px solid var(--border);
}
.match-bar-fill {
  height: 100%;
  background: linear-gradient(to right,var(--teal),var(--navy));
  border-radius: 999px;
}
.match-actions {
  display: flex;
  gap: .75rem;
  justify-content: center;
  margin-top: 1.1rem;
  flex-wrap: wrap;
}
.match-btn {
  border-radius: 999px;
  padding: .8rem 1.2rem;
  font-weight: 900;
  border: 1px solid var(--border);
  background: #fff;
  color: var(--navy);
  display: inline-flex;
  align-items: center;
  gap: .55rem;
  transition: transform .14s ease, box-shadow .14s ease, border-color .14s ease;
}
.match-btn:hover {
  transform: translateY(-1px);
  border-color: rgba(47,196,182,.35);
  box-shadow: 0 14px 30px rgba(34,49,75,.10);
}
.match-btn.primary {
  background: var(--teal);
  border-color: var(--teal);
  color: #fff;
}
.match-btn.ghost {
  background: rgba(255,246,239,.45);
}
.match-foot {
  margin-top: 1rem;
  text-align: center;
}
@media (max-width: 720px) {
  .q-options {
    grid-template-columns: 1fr;
  }
}
.auth-split {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1.1fr .9fr;
  background: var(--cream);
}
.auth-left {
  padding: 5.5rem 3rem 3rem;
  display: flex;
  align-items: center;
}
.auth-left-inner {
  max-width: 560px;
}
.eyebrow {
  display: inline-flex;
  align-items: center;
  gap: .5rem;
  padding: .35rem .85rem;
  border-radius: 999px;
  background: rgba(47,196,182,.10);
  color: var(--navy);
  font-weight: 700;
  font-size: .85rem;
  border: 1px solid var(--border);
}
.auth-title {
  margin: 1rem 0 .75rem;
  font-size: 2.35rem;
  line-height: 1.1;
  font-weight: 850;
  color: #111827;
}
.auth-title span {
  color: var(--teal);
}
.auth-lead {
  color: #111827;
  opacity: .86;
  font-size: 1.02rem;
  line-height: 1.6;
  margin-bottom: 1.25rem;
}
.auth-points {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  gap: .65rem;
}
.auth-points li {
  display: flex;
  align-items: flex-start;
  gap: .7rem;
  color: var(--ink-2);
  font-weight: 650;
}
.auth-right {
  padding: 5.5rem 2.2rem 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-left: 1px solid var(--border);
  background: rgba(255,255,255,.35);
  backdrop-filter: blur(10px);
}
.auth-card {
  width: 100%;
  max-width: 440px;
  background: #fff;
  border: 1px solid var(--border);
  border-radius: 22px;
  padding: 1.5rem 1.4rem;
  box-shadow: 0 18px 60px rgba(34,49,75,.10);
  transform: translateY(0);
  transition: transform .18s ease, box-shadow .18s ease;
}
.auth-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 22px 70px rgba(34,49,75,.14);
}
.auth-card-head h2 {
  margin: 0;
  font-weight: 850;
  color: var(--navy);
}
.muted {
  color: var(--ink-2);
}
.auth-card-head .muted {
  margin: .35rem 0 0;
}
.oauth-row {
  display: grid;
  gap: .6rem;
  margin: 1rem 0 .9rem;
}
.oauth-btn {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: .55rem;
  border-radius: 14px;
  border: 1px solid var(--border);
  padding: .75rem 1rem;
  background: rgba(255,255,255,.9);
  font-weight: 750;
  color: var(--navy);
  transition: transform .14s ease, box-shadow .14s ease, border-color .14s ease;
}
.oauth-btn:hover {
  transform: translateY(-1px);
  border-color: rgba(47,196,182,.35);
  box-shadow: 0 14px 30px rgba(34,49,75,.10);
}
.divider {
  position: relative;
  text-align: center;
  margin: .9rem 0;
}
.divider:before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  height: 1px;
  background: var(--border);
}
.divider span {
  position: relative;
  z-index: 1;
  padding: 0 .75rem;
  background: #fff;
  color: var(--muted);
  font-weight: 750;
  font-size: .9rem;
}
.form-field {
  margin: .85rem 0;
}
.form-field label {
  display: block;
  font-weight: 750;
  color: var(--navy);
  margin-bottom: .35rem;
  font-size: .92rem;
}
.ss-input {
  width: 100%;
  border-radius: 14px;
  border: 1px solid var(--border);
  padding: .75rem .9rem;
  outline: none;
  font-size: .95rem;
  color: #111827;
  background: #fff;
}
.ss-input:focus {
  border-color: rgba(47,196,182,.55);
  box-shadow: 0 0 0 4px rgba(47,196,182,.14);
}
.auth-msg {
  margin: .75rem 0;
  padding: .75rem .85rem;
  border-radius: 14px;
  background: rgba(47,196,182,.10);
  border: 1px solid rgba(47,196,182,.25);
  color: var(--navy);
  font-weight: 650;
}
.auth-primary {
  margin-top: .65rem;
}
.auth-secondary {
  margin-top: .6rem;
}
.auth-footnote {
  margin: .9rem 0 0;
  font-size: .78rem;
  color: var(--muted);
  line-height: 1.45;
}
.auth-shell {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5rem 1.25rem 2rem;
  background: var(--cream);
}
.auth-loader {
  margin-top: 1rem;
  height: 10px;
  border-radius: 999px;
  background: linear-gradient(90deg, rgba(47,196,182,.15), rgba(34,49,75,.15), rgba(47,196,182,.15));
  background-size: 200% 100%;
  animation: ssload 1.1s linear infinite;
}
@keyframes ssload {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 200% 0;
  }
}
.onb-shell {
  min-height: 100vh;
  padding: 6rem 1.25rem 2.25rem;
  background: var(--cream);
  display: flex;
  justify-content: center;
}
.onb-card {
  width: 100%;
  max-width: 860px;
  background: #fff;
  border: 1px solid var(--border);
  border-radius: 22px;
  padding: 1.6rem 1.5rem;
  box-shadow: 0 18px 60px rgba(34,49,75,.10);
}
.onb-badge {
  display: inline-flex;
  align-items: center;
  gap: .55rem;
  padding: .35rem .85rem;
  border-radius: 999px;
  background: rgba(47,196,182,.10);
  border: 1px solid rgba(47,196,182,.25);
  font-weight: 800;
  color: var(--navy);
  font-size: .86rem;
}
.onb-head h1 {
  margin: .9rem 0 .35rem;
  font-weight: 900;
  color: #111827;
  letter-spacing: -.02em;
}
.onb-grid {
  display: grid;
  grid-template-columns: repeat(2,minmax(0,1fr));
  gap: .85rem;
  margin: 1.25rem 0 1.35rem;
}
.onb-item {
  display: flex;
  gap: .85rem;
  padding: 1rem 1rem;
  border: 1px solid var(--border);
  border-radius: 18px;
  background: rgba(255,246,239,.55);
}
.onb-item i {
  font-size: 1.2rem;
  color: var(--teal);
  margin-top: .1rem;
}
.onb-item strong {
  color: var(--navy);
}
.onb-actions {
  display: flex;
  gap: .75rem;
  flex-wrap: wrap;
  justify-content: flex-end;
  margin-top: .9rem;
}
.q-stack {
  display: grid;
  gap: .85rem;
  margin-top: 1.1rem;
}
.q-card {
  border: 1px solid var(--border);
  border-radius: 18px;
  padding: 1rem;
  background: #fff;
}
.q-title {
  font-weight: 850;
  color: var(--navy);
  margin-bottom: .25rem;
}
.q-helper {
  font-size: .9rem;
  margin-bottom: .55rem;
}
.q-input {
  min-height: 92px;
  resize: vertical;
}
@media (max-width: 920px) {
  .auth-split {
    grid-template-columns: 1fr;
  }
  .auth-right {
    border-left: none;
    border-top: 1px solid var(--border);
  }
  .auth-left {
    padding: 5.2rem 1.5rem 1.25rem;
  }
  .auth-right {
    padding: 1.25rem 1.25rem 2.25rem;
  }
  .auth-title {
    font-size: 2.0rem;
  }
  .onb-grid {
    grid-template-columns: 1fr;
  }
}
.password-wrap {
  position: relative;
  display: flex;
  align-items: center;
}
.password-wrap .ss-input {
  padding-right: 44px;
}
.pw-toggle {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  border: 0;
  background: transparent;
  padding: 6px;
  line-height: 1;
  cursor: pointer;
  hookup: none;
  opacity: 0.85;
}
.pw-toggle:disabled {
  cursor: not-allowed;
  opacity: 0.55;
}
.pw-toggle i {
  font-size: 1.1rem;
}
.pw-rules {
  margin-top: 10px;
  padding: 10px 12px;
  border: 1px solid rgba(29, 43, 58, 0.12);
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.6);
}
.pw-help {
  margin: 0 0 8px 0;
  font-size: 12px;
  line-height: 1.35;
  color: rgba(29, 43, 58, 0.75);
  font-weight: 600;
}
.pw-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  gap: 6px;
}
.pw-item {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 12px;
  line-height: 1.25;
  font-weight: 600;
}
.pw-item i {
  font-size: 14px;
}
.pw-item.ok {
  color: rgba(20, 110, 70, 0.95);
}
.pw-item.bad {
  color: rgba(150, 35, 35, 0.9);
}